.showFullItem {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(128, 128, 128, 0.629);
  z-index: 10;
  overflow: auto;
}
.fullItem {
  position: relative;
  width: 50%;
  background-color: #f5f5f5;
  overflow: hidden;
  margin: 20px auto;
  padding-bottom: 10px;
  border-radius: 16px;
}
.fullItem img {
  width: 100%;
  border: 10px 10px 0 0;
  transition: transform 500ms ease;
  cursor: pointer;
}
.closed {
  position: absolute;
  top: 15px;
  right: 20px;
  font-weight: 600;
  cursor: pointer;
  border: 1px solid black;
  border-radius: 50%;
  padding: 3px 5px;
  background-color: rgba(165, 42, 42, 0.656);
}
.fullItem img:hover {
  transform: scale(1.2);
}

.fullItem h3 {
  text-transform: capitalize;
  padding: 3px;
  margin-left: 5px;
}
.fullItem p,
.fullItem b {
  padding: 5px;
  margin-left: 10px;
}
.addToCart {
  position: absolute;
  right: 20px;
  bottom: 20px;
  font-weight: 600;
  background-color: chocolate;
  color: #fff;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
  font-size: 18px;
  line-height: 30px;
  transition: transform 500ms ease;
}

.addToCart:hover {
  transform: scale(1.3) translateY(-5px);
}
