.item {
  width: 30%;
  margin-bottom: 40px;
  background-color: #f5f5f5;
  overflow: hidden;
  position: relative;
  margin-bottom: 20px;
  padding-bottom: 20px;
}

.item img {
  width: 100%;
  border: 10px 10px 0 0;
  transition: transform 500ms ease;
  cursor: pointer;
}
.item img:hover {
  transform: scale(1.5);
}

.item h3 {
  text-transform: capitalize;
  padding: 3px;
  margin-left: 5px;
}
.item p,
.item b {
  padding: 5px;
  margin-left: 10px;
}
.addToCart {
  position: absolute;
  right: 15px;
  bottom: 15px;
  font-weight: 600;
  background-color: chocolate;
  color: #fff;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
  font-size: 18px;
  line-height: 30px;
  transition: transform 500ms ease;
  
}

.addToCart:hover {
  transform: scale(1.3) translateY(-5px);
}
